@import url(https://fonts.googleapis.com/css2?family=Advent+Pro:wght@200;400;500;700&display=swap);
body {
  font-family: 'Advent Pro', sans-serif;
  font-size: 16px;
  color: #666665;
}

h1 {
  font-size: 54px;
  font-weight: bold;
}

h2 {
  font-size: 40px;
  font-weight: 700;
}

h4 {
  font-size: 32px;
}

h5 {
  font-size: 24px;
}

img {
  display: block;
  width: 100%;
}

strong {
  font-weight: 700;
}

p {
  letter-spacing: 0.34px;
  line-height: 22px
}

.myButton {
  display: block;
  background-color: #FF576F;
  border-radius: 50px;
  padding: 8px 24px;
  font-size: 18px;
  font-weight: 500;
  color: white;
  text-transform: uppercase;
  text-align: center;
}

.myButton.mini {
  padding: 8px 16px;
  font-size: 12px;

}

.myButton.mini.outlined {
  color: #FF576F;
  background-color: transparent;
  border: none;
  padding: 0;
  font-size: 12px;
}

footer {
  height: 60px;
  width: 100%;
}

footer p {
  color: #B39898;
  font-size: 10px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 12px;
  text-align: center;
}

/* Modal syle handler */

.modal-backdrop {
  background-color: #D9A5A6;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px)
}

.myModal .modal-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 200px;
  border: 1px solid #D9A5A6;
  border-radius: 20px;
  background-color: #FFFFFF;
  padding: 32px;
}



.form-label {
  font-size: 14px;
  margin: 4px 0;
}

.form-input {
  background-color: rgba(252, 204, 210, 0.1);
  border: none;
  border-bottom: solid #FCCCD2 2px;
  width: 368px;
}

.form-button {
  background-color: #FF576F;
  border-radius: 50px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 16px;
  padding: 8px 24px;
  border: none;
  margin: 32px auto 16px;
  display: block;
}


.form-input::-webkit-input-placeholder {
  font-size: 12px;
}


.form-input:-ms-input-placeholder {
  font-size: 12px;
}


.form-input::-ms-input-placeholder {
  font-size: 12px;
}


.form-input::placeholder {
  font-size: 12px;
}

.myModal .mini-link {
  color: #6EA8B5;
  border: none;
}

.myForm .form-input.file-upload {
  border: none;
}

.myForm .form-input.file-upload::input {
  border: none;
}

button {
  border: none;
  padding: 0;
  margin: 0;
}

.menu {
    background-color: white;
    padding: 24px 0;
}

.flex-container {
    display: flex;
