.portfolioCreator {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 256px;
    padding: 24px;
}

.portfolioCreator .allElements {
    min-width: 100%;
    display: flex;

}

.portfolioCreator .allElements .selector {
    width: 20%;
    height: 100vh;
    overflow: scroll;
    padding: 24px;
    background-color: #FDFBFB;
    border-radius: 20px;

}

.portfolioCreator .allElements .selector .sectionTitle {
    color: #B39898;
    font-weight: 700;
    margin: 24px 0;
    font-size: 21px;
    text-align: center;
    border-top: #B39898 1px solid;
    border-bottom: #B39898 1px solid;
    padding: 24px 0;
}

.portfolioCreator .allElements .selector .selectorTag {
    background-color: #f5f6f9;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    overflow: hidden;
    max-width: 95%;
    padding: 16px;
    border-radius: 10px;
    margin: 12px 8px;
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

.portfolioCreator .allElements .portfolioConstructor {
    width: 79%;
    margin-left: 1%;
    height: 100vh;
    border: 6px solid #FDFBFB;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    overflow: scroll;
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.portfolioCreator .allElements .portfolioConstructor .creatorHeader {
    width: 100%;
    background-color: khaki;
    padding: 80px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.portfolioCreator .allElements .portfolioConstructor .avatarReceptor {
    background-color: #f5f6f9;
    width: 200px;
    height: 200px;
    border-radius: 150px;
    margin-top: -120px;
    overflow: hidden;
}

.portfolioCreator .allElements .portfolioConstructor .creatorHeader .newAvatar {
    display: block;
    object-fit: contain;
    object-position: center;
    height: 200px;
}

.portfolioCreator .allElements .portfolioConstructor .portfolioContent {
    background-color: lightblue;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    border-radius: 20px;
    margin: 40px 0 0;
    padding: 24px;
}

.portfolioCreator .allElements .portfolioConstructor .portfolioContent .endContainer {
    width: 47%;
    margin: 12px 1%;
    background-color: #FDFBFB;
    padding: 24px;
    min-height: 150px;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
}

.portfolioCreator .allElements .portfolioConstructor .portfolioContent .endContainer.workReceptor {
    width: 90%;
}

.portfolioCreator .allElements .portfolioConstructor .portfolioContent .endContainer .newAdd,
.portfolioCreator .allElements .portfolioConstructor .portfolioContent .endContainer .workReceptor .newAdd {
    border: solid 1px #FCCCD2;
